:root {
  --font-size: 17px;
  --line-height: 1.7;
  --bg-main: #0b0b12;
  --bg-surface: #12121c;
  --bg-input: #181827;
  --accent: #b9a3ff;
  --accent-soft: #b9a3ff59;
  --accent-glow: #b9a3ff2e;
  --text-main: #e8e6f0;
  --text-muted: #e8e6f08c;
  --border: #b9a3ff26;
  color-scheme: dark;
}

@media (prefers-color-scheme: light) {
  :root {
    --bg-main: #f6f5fb;
    --bg-surface: #fff;
    --bg-input: #f0eef8;
    --text-main: #1a1824;
    --text-muted: #1a18248c;
    --accent: #7b5cff;
    --accent-soft: #7b5cff59;
    --accent-glow: #7b5cff2e;
    --border: #1a18241f;
    color-scheme: light;
  }
}

* {
  box-sizing: border-box;
}

::selection {
  background: #b9a3ff40;
}

html {
  font: var(--font-size) / var(--line-height) Roboto, sans-serif;
  background: var(--bg-main);
  color: var(--text-main);
  word-break: break-word;
  min-width: 320px;
  overflow-y: auto;
}

body {
  background: var(--bg-main);
  border: 1px solid var(--border);
  border-bottom: none;
  flex-direction: column;
  min-height: 100vh;
  margin: 0;
  display: flex;
}

a {
  color: var(--accent);
  text-decoration-color: var(--accent-soft);
}

main {
  flex-direction: column;
  flex-grow: 1;
  margin: 0 20px;
  padding-bottom: 40px;
  display: flex;
  position: relative;
}

main > * {
  margin: 0 0 calc(var(--font-size) * var(--line-height));
}

main > :not(h2), footer > :not(input) {
  width: 100%;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

img {
  max-width: 100%;
  height: auto;
}

h1 {
  text-align: center;
  margin: 7vh auto .5em;
  font-size: 2.4em;
  font-weight: 300;
  line-height: 1.1;
}

h1 img {
  opacity: .92;
  width: 280px;
  margin: auto;
  display: block;
}

h1, h2, h3 {
  font-family: Roboto Slab, serif;
  font-weight: 300;
}

h2 {
  color: var(--text-muted);
  font-size: 28px;
  display: flex;
}

h2:before, h2:after {
  content: "";
  border-bottom: 1px solid var(--border);
  flex-grow: 1;
  margin-bottom: 1.2ex;
}

h2:before {
  margin-right: 16px;
}

h2:after {
  margin-left: 16px;
}

input[type="text"], input[type="url"], textarea, button {
  background-color: var(--bg-input);
  font: inherit;
  color: var(--text-main);
  text-align: center;
  border: 1px solid var(--border);
  border-radius: .45em;
  outline: none;
  margin: .5em 0;
  padding: .45em .6em;
  transition: border-color .15s, box-shadow .15s, background-color .15s;
}

input::placeholder {
  color: var(--text-muted);
}

input[type="url"], input[type="text"], textarea {
  width: 100%;
}

textarea {
  resize: vertical;
  font-size: 11px;
  line-height: 1.4;
}

@keyframes focusPulse {
  0% {
    box-shadow: 0 0 0 1px var(--accent-soft);
  }

  50% {
    box-shadow: 0 0 0 1px var(--accent), 0 0 8px var(--accent-glow);
  }

  100% {
    box-shadow: 0 0 0 1px var(--accent-soft);
  }
}

textarea:focus, input[type="text"]:focus, input[type="url"]:focus {
  background-color: var(--bg-surface);
  border-color: var(--accent);
  animation: 2.8s ease-in-out infinite focusPulse;
}

@media (prefers-reduced-motion: reduce) {
  textarea:focus, input[type="text"]:focus, input[type="url"]:focus {
    animation: none;
  }
}

button {
  cursor: pointer;
  background-color: #0000;
}

button:hover, .close-panel:hover {
  border-color: var(--accent);
  color: var(--accent);
  box-shadow: 0 0 6px var(--accent-glow);
}

ol {
  padding-left: 0;
  list-style: none;
}

ul {
  padding-left: 3.5em;
  list-style: none;
}

ul li:before {
  content: "→ ";
  color: var(--accent-soft);
  margin-left: -1.5em;
  position: absolute;
}

.text-monospace {
  font-family: monospace !important;
}

.text-pre {
  white-space: pre !important;
}

.d-inline-block {
  display: inline-block;
}

.d-block {
  display: block;
}

.text-align-center {
  text-align: center;
}

.layer-top {
  padding-top: calc(var(--font-size) * var(--line-height));
}

.view-toggle [type="radio"] {
  visibility: hidden;
  position: absolute;
}

.view-toggle .panels {
  position: relative;
}

.token, .info, .proxy {
  display: none;
}

#info-toggle:checked ~ .info, #token-toggle:checked ~ .token {
  display: block;
}

.close-panel {
  border: 1px solid var(--border);
  color: var(--text-muted);
  border-radius: .4em;
  padding: 0 .55em;
  position: absolute;
  top: 0;
  right: 10px;
  text-decoration: none !important;
}

#none-toggle:checked ~ .close-panel {
  display: none;
}

header {
  background-color: var(--bg-surface);
  text-align: center;
  border-bottom: 1px solid var(--border);
  padding: 2.5em;
}

.validation:invalid:not(:placeholder-shown) {
  color: #ea868f;
  background-color: #ea868f1f;
  border-color: #ea868f;
}

.validation:valid {
  border-color: var(--accent);
  background-color: #b9a3ff1f;
}

pre {
  background: var(--bg-input);
  border: 1px solid var(--border);
  border-radius: .4em;
  padding: 10px;
  font-size: 11px;
  overflow: auto;
}

pre div:first-child {
  color: var(--accent);
  text-shadow: 0 0 6px var(--accent-glow);
  font-size: 18px;
}
